* {
	font-family: 'Roboto', sans-serif; }
a {
	text-decoration: none;
	&:hover {
		text-decoration: underline; } }
header {
	position: relative;
	z-index: 1; }
nav {
	height: 50px;
	background-color: #212121; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	.menu_link {
		color: #FFF;
		font-size: 15px;
		font-style: normal;
		font-weight: 300; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 37px; } }
.container {
	.row {
		.btn_text {
			width: 172px;
			height: 46px; } } }
.subheader {
	position: absolute;
	width: 100%;
	height: 87px;
	background-color: rgba(0, 0,0, 0.6);
	z-index: 2;
	.subheader_logo {
		display: block;
		margin-top: 19px;
		max-width: 100%; }
	.subheader_official {
		margin-top: 4px;
		color: #FFF;
		font-size: 13px;
		font-weight: 100; }
	.subheader_call {
		margin-top: 24px;
		color: #FFF;
		text-align: right;
		font-size: 12px;
		font-weight: 300;
		line-height: 14px; }
	.subheader_phone {
		display: block;
		color: #FFF;
		text-align: right;
		font-size: 22px;
		font-style: normal;
		font-weight: 400;
		text-transform: uppercase;
		line-height: 30px;
		margin-top: 5px; }
	.btn_text {
		border: none;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset;
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		text-transform: uppercase;
		margin-top: 23px; } }
.promo {
	min-height: 799px;
	background: url(../img/bg/uber_bg_jpg.jpg) center center/cover no-repeat;
	padding: 155px 0 186px 0;
	.promo_header {
		color: #FFF;
		text-align: center;
		font-size: 60px;
		font-style: normal;
		font-weight: 900;
		line-height: 60px;
		text-transform: uppercase;
		margin: 0; }
	.promo_subheader {
		color: #FFF;
		font-size: 36px;
		font-style: normal;
		font-weight: 700;
		line-height: 36px;
		text-transform: uppercase;
		text-align: center;
		margin: 0;
		margin-top: 20px; }
	.promo_descr {
		color: #FFF;
		text-align: center;
		font-size: 18px;
		font-style: normal;
		font-weight: 400;
		line-height: 24px;
		margin-top: 16px; }
	.promo_btn {
		display: block;
		width: 259px;
		height: 63px;
		flex-shrink: 0;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.12) inset;
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 18px;
		font-style: normal;
		font-weight: 400;
		text-transform: uppercase;
		border: none;
		margin: 115px auto 0 auto; } }
.advantages {
	padding: 77px 0 115px 0;
	.advantages_quantity {
		color: #222;
		text-align: center;
		font-size: 38px;
		font-style: normal;
		font-weight: 700;
		line-height: 48px;
		text-transform: uppercase;
		margin-bottom: 0px; }
	.advantages_round {
		width: 116px;
		height: 116px;
		flex-shrink: 0;
		background-color: #1EACC7;
		border-radius: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 25px; }
	.advantages_descr {
		.subtitle {
			color: #1EACC7;
			font-size: 17px;
			font-style: normal;
			font-weight: 400;
			line-height: 21px;
			max-width: 343px; }
		.subtitle_texte {
			display: block;
			color: #202020;
			font-size: 15px;
			font-style: normal;
			font-weight: 300;
			line-height: 21px;
			max-width: 372px;
			margin-top: 5px; } }
	.advantages_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 16px; }
	.advantages_block_block {
		.advantages_descr_descr {
			display: flex;
			flex-direction: column;
			justify-content: start;
			width: 343px;
			.subtitle {
				color: #1EACC7;
				font-size: 17px;
				font-style: normal;
				font-weight: 400;
				line-height: 21px; }
			.subtitle_texte {
				display: block;
				color: #202020;
				font-size: 15px;
				font-style: normal;
				font-weight: 300;
				line-height: 21px;
				width: 372px;
				margin-top: 5px; } } } }
.advantages_block_block {
	display: flex;
	justify-content: center;
	margin-top: 16px;
	padding: 0 23px 0 0; }
